import { saveRowsDirect } from "./actions";
import moment from 'moment';
//helper method to swap two fields (usecase: user input under a "dropdown" conditional select):
// https://github.com/final-form/react-final-form#conditional-fields
export const overrideAFieldValue = (values, sourceField, destinationField) => {
    values[destinationField] = values[sourceField];
    return values;
};
export const submitAnimalRequest = (values, qcstate) => {
    console.log(values);
    let currentDate = moment(new Date()).format();
    let taskid = LABKEY.Utils.generateUUID().toUpperCase();
    let assignedTo = LABKEY.Security.currentUser.id;
    let weightValToInsert = [];
    let jsonData;
    //we need to copy the values before the insert because if we update the values of the form then it messes it up
    let valuesToBeInserted = Object.assign({}, values);
    valuesToBeInserted['date'] = currentDate;
    valuesToBeInserted['QCState'] = qcstate;
    valuesToBeInserted['taskid'] = taskid;
    //replace the principalinvestigator (PI) field if there's custom input from an external PI
    if (valuesToBeInserted['externalprincipalinvestigator']) {
        let sourceField = 'externalprincipalinvestigator';
        let destinationField = 'principalinvestigator';
        let newvalues = overrideAFieldValue(valuesToBeInserted, sourceField, destinationField);
        valuesToBeInserted = newvalues;
    }
    weightValToInsert.push(valuesToBeInserted);
    console.log(weightValToInsert);
    let taskIdValToInsert = {
        initialQCState: 'Completed',
        childRecords: null,
        //existingRecords: weightValToInsert,
        taskRecord: {
            taskId: taskid,
            duedate: currentDate,
            assignedTo: assignedTo,
            category: "task",
            title: "Animal Request",
            formType: "Animal Request"
        }
    };
    jsonData =
        {
            commands: [
                {
                    schemaName: "wnprc",
                    queryName: "animal_requests",
                    command: "insert",
                    rows: weightValToInsert
                },
                {
                    schemaName: "ehr",
                    queryName: "tasks",
                    command: "insert",
                    initialQCState: 'Completed',
                    childRecords: null,
                    existingRecords: null,
                    rows: [taskIdValToInsert.taskRecord]
                }
            ],
        };
    return saveRowsDirect(jsonData);
};
export const sendNotificationEmail = (from, subject, recipients, content) => {
    function errorHandler(errorInfo, responseObj) {
        LABKEY.Utils.displayAjaxErrorResponse(responseObj, errorInfo);
        console.log(responseObj);
        console.log(errorInfo);
    }
    function onSuccess(result) {
        //TODO log to server?
        //alert('Message sent successfully.');
    }
    LABKEY.Message.sendMessage({
        msgFrom: from,
        msgSubject: subject,
        msgRecipients: [
            LABKEY.Message.createRecipient(LABKEY.Message.recipientType.to, recipients)
        ],
        msgContent: [
            LABKEY.Message.createMsgContent(LABKEY.Message.msgType.html, content),
        ],
        success: onSuccess,
        failure: errorHandler,
    });
};
export const constructNotficationEmail = (data) => {
    try {
        let rowid = (data['result'][0]['rows'][0].rowid).toString();
        let domain = window.location.origin;
        let content = 'Click ' +
            '<a href="' + domain + '/ehr/WNPRC/EHR/manageRecord.view?schemaName=wnprc&queryName=animal_requests&keyField=rowid&key=' + rowid + '">' +
            'here</a> to view the request.<br><br>' +
            'View all of the animal requests ' + '' +
            '<a href="' + domain + '/wnprc_ehr/WNPRC/EHR/dataEntry.view?#topTab:Requests&activeReport:AnimalRequests"' +
            ' >here</a>.';
        return content;
    }
    catch (err) {
        console.log(err);
        return null;
    }
};



