//Returns most recent demographics given animal id
export function getMostRecentDemographics(id) {
    return new Promise((resolve, reject) => {
        return LABKEY.Query.selectRows({
            schemaName: 'study',
            queryName: 'demographics',
            containerPath: '/WNPRC/EHR',
            sort: '-date',
            filterArray: [
                LABKEY.Filter.create('Id', id, LABKEY.Filter.Types.EQUAL)
            ],
            success: (data) => {
                resolve(data);
            },
            failure: (data) => {
                reject(data);
            }
        });
    });
}
//Expects json row data with commands property, see API docs for saveRows
export function saveRowsDirect(jsonData) {
    return new Promise((resolve, reject) => {
        LABKEY.Query.saveRows({
            commands: jsonData.commands,
            method: 'POST',
            containerPath: '/WNPRC/EHR',
            success: (data) => {
                resolve(data);
            },
            failure: (data) => {
                reject(data);
            }
        });
    });
}
//TODO need to use a specific queryname and make it an sql file?
export function selectRowsSql(id) {
    return new Promise((resolve, reject) => {
        return LABKEY.Query.selectRows({
            schemaName: 'study',
            queryName: 'demographicWeightChange',
            columns: 'participantid,weight',
            success: (data) => {
                resolve(data);
            },
            failure: (data) => {
                reject(data);
            }
        });
    });
}
//filters for project
export function getEHRData(schemaName, queryName, sort = 'rowid', columns = '', filterArray = []) {
    return new Promise((resolve, reject) => {
        return LABKEY.Query.selectRows({
            schemaName: schemaName,
            queryName: queryName,
            columns: columns,
            sort: sort,
            containerPath: '/WNPRC/EHR',
            filterArray: filterArray,
            success: (data) => {
                resolve(data);
            },
            failure: (data) => {
                reject(data);
            }
        });
    });
}



